.imgbox img { max-width: 95%; max-height: 80%; position: absolute; top: 50%; left: 50%; transform: translate3d(-50%, -50%, 0); border-radius: 4px; animation: enterImg 0.4s ease-out; }

.imgbox span { position: absolute; width: 80%; color: #fff; left: 50%; padding: 10px 12px; transform: translateX(-50%); }

@keyframes enterImg { from { transform: translate3d(-50%, -50%, 0) scale(0); }
  to { transform: translate3d(-50%, -50%, 0) scale(1); } }

/**********************
*   Accordion         *
*   Updated: 160317   *
***********************
*
*/
.accordion { width: 100%; background-color: #fff; }

.accordion ul.accordion-list { width: 100%; transition: .3s ease-out transform; border-top: 1px solid #ddd; border-right: 1px solid #ddd; border-left: 1px solid #ddd; }

.accordion ul.accordion-list li { float: none; width: 100%; display: block !important; border-bottom: 1px solid #ddd; }

.accordion ul.accordion-list li > a { transition: background-color .3s; font-size: 1rem; color: #000; display: block; padding: 0 1rem; text-align: left; color: rgba(33, 33, 33, 0.8); font-weight: 500; line-height: 48px; }

.accordion ul.accordion-list li > a.active { background-color: rgba(180, 180, 180, 0.3); }

.accordion ul.accordion-list li > a i { margin: 0 .5rem; line-height: inherit; }

.accordion .accordion-content { display: none; padding: .5rem 1.5rem; height: auto; position: relative; border-top: 1px solid #ddd; }

/**********************
*   Tabs            *
***********************
*
*/
.tabular .tabs-nav { display: inline-block; width: 100%; margin-bottom: 0; background-color: #fafafa; white-space: nowrap; overflow: hidden; overflow-x: auto; }

.tabular .tabs-nav li { display: inline-block; margin: 4px; padding: 0; }

.tabular .tabs-nav li > a { padding: 0 0.75rem; margin: 0; display: inline-block; color: #212121; line-height: 3; border-radius: 2px; transition: .3s; }

.tabular .tabs-nav li > a:hover, .tabular .tabs-nav li > a:active { background-color: rgba(180, 180, 180, 0.3); }

.tabular .tabs-nav li.active > a { color: #fff; background-color: #2196F3; }

.tabular .tabs-content { background-color: #fff; padding: 1rem 1.5rem; }

.tabular .tabs-content .tabs-panel { display: none; }

.tabular .tabs-content .tabs-panel.active { display: block; }

/**********************
*   Dropdown menu     *
***********************
*
*/
.dropdown-wrapper { display: inline-block; position: relative; overflow: visible; }

.dropdown-wrapper a { padding: 0 1rem; display: block; white-space: nowrap; transition: 0.3s; }

.dropdown-wrapper .drop-content { display: none; position: absolute; min-width: 170px; max-height: 350px; margin: 0 0.5rem; overflow-y: auto; line-height: 3rem; text-align: left; border-radius: 2px; background-color: white; z-index: 998; }

.dropdown-wrapper .drop-content.pop-left { right: 0; }

.dropdown-wrapper .drop-content.pop-up { bottom: 100%; }

.dropdown-wrapper .drop-content.pop-up-left { bottom: 100%; right: 0; }

.dropdown-wrapper .drop-content li { position: relative; }

.dropdown-wrapper .drop-content li a:hover { background-color: rgba(0, 0, 0, 0.08); }

/**********************
*   Footer            *
***********************
*
*/
footer { color: #000; background-color: #fafafa; }

footer a { color: #000; }

footer .footer-content { padding-top: 2rem; padding-bottom: 2rem; }

footer .footer-copyright { overflow: hidden; height: 50px; line-height: 50px; color: #000; background-color: rgba(0, 0, 0, 0.12); }

